import React from 'react';
import * as microsoftTeams from "@microsoft/teams-js";
import querystring from 'querystring';
import './../App.css';

function getsevertoken(context, tokens) {
  var tid = context.tid;
  var token = tokens;
  var scopes = ["https://graph.microsoft.com/User.Read"];

  return new Promise((resolve, reject) => {
    const url = "https://login.microsoftonline.com/common/oauth2/v2.0/token";
    const localurl = "http://localhost:3333/auth/token";
    const params = {
      client_id: '630f584b-b191-409e-a327-e0d185632359',
      client_secret: '91M_kPSR6h4-jhGl7disIg_bwhNcBXvAS~',
      grant_type: "client_credentials",
      assertion: token,
      requested_token_use: "on_behalf_of",
      scope: scopes.join(" ")
    };

    fetch(localurl, {
      method: "POST",
      body: querystring.stringify(params),
      headers: {
        Accept: "application/json",
        "Content-Type": "application/x-www-form-urlencoded"
      }
    }).then(result => {
      if (result.status !== 200) {
        result.json().then(json => {
          // TODO: Check explicitly for invalid_grant or interaction_required
          reject({ "error": json.error });
        });
      } else {
        result.json().then(json => {
          resolve(json.access_token);
        });
      }
    });
  });

}



function getClientSideToken() {

  return new Promise((resolve, reject) => {

    console.log("1. Get auth token from Microsoft Teams");

    microsoftTeams.authentication.getAuthToken({
      successCallback: (result) => {
        //display(result)
        //console.log(result);
        resolve(result);
      },
      failureCallback: function (error) {
        reject("Error getting token: " + error);
        console.log('Error getting token');
      }
    });

  });

}

// 2. Exchange that token for a token with the required permissions
//    using the web service (see /auth/token handler in app.js)
function getServerSideToken1(clientSideToken) {

  //display("2. Exchange for server-side token");

  return new Promise((resolve, reject) => {

    microsoftTeams.getContext((context) => {

      getsevertoken(context, clientSideToken)
        .then((response) => {
          if (response.ok) {
            return response.json();
          } else {
            reject(response.error);
          }
        })
        .then((responseJson) => {
          if (responseJson.error) {
            reject(responseJson.error);
          } else {
            const serverSideToken = responseJson;
            //display(serverSideToken);
            resolve(serverSideToken);
          }
        });
    });
  });
}

function getServerSideToken(clientSideToken) {

  console.log("2. Exchange for server-side token");

  return new Promise((resolve, reject) => {

    microsoftTeams.getContext((context) => {

      fetch('http://localhost:3333/auth/token', {
        method: 'post',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          'tid': context.tid,
          'token': clientSideToken
        }),
        mode: 'cors',
        cache: 'default'
      })
        .then((response) => {
          if (response.ok) {
            return response.json();
          } else {
            reject(response.error);
          }
        })
        .then((responseJson) => {
          if (responseJson.error) {
            reject(responseJson.error);
          } else {
            const serverSideToken = responseJson;
            console.log(serverSideToken);
            resolve(serverSideToken);
          }
        });
    });
  });
}
