import React from 'react';
import * as microsoftTeams from "@microsoft/teams-js";
import querystring from 'querystring';
import './../App.css';



class Notification extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            client_token: 'client token',
            server_token: 'server token',
            profile: {},
            theme: 'theme',
            name: 'name',
            context: {}
        }

    }

    componentDidMount() {

        
        var theme = localStorage.getItem("theme");

        if(theme){
            alert('Theme is '+theme);
        }else{
            alert('No theme local storage');
        }

        let currentComponent = this;

        microsoftTeams.initialize();

        microsoftTeams.getContext(function (context) {

            if (context && context.theme) {

                console.log(context);

                currentComponent.setState({
                    theme: context.theme,
                    context: context
                });

                localStorage.setItem("theme", context.theme);

            } else {
                console.log('no theme');
            }
        });
        microsoftTeams.authentication.getUser(function (data){
            console.log('getUser')
            console.log(data)
        })
  
        console.log('showNotification')
        microsoftTeams.showNotification(function (data){
            console.log('getUserJoinedTeams')
            console.log(data)
        });

       
      

        return  new Promise((resolve, reject) => {
            const url = "http://hsenidapps.peopleshr.com:9095/MSTEAMS/api/v1/intapi/authclientuser";
            const url2 = "http://localhost:3333/hrm/auth"
            const params = {
                "UserID": "chamath",
                "UserPassword" : "chamath123*",
                "ClientID": 1,
                "GroupID" : 1
                };
        
            fetch(url2, {
              method: "POST",
              body: querystring.stringify(params),
              headers: {
                Accept: "application/json",
                "Content-Type": "application/x-www-form-urlencoded"
              }
            }).then(result => {
                console.log(result)
              if (result.status !== 200) {
                result.json().then(json => {
                  // TODO: Check explicitly for invalid_grant or interaction_required
                  console.log('error notification');
                  reject({"error":json.error});
                });
              } else {
                result.json().then(json => {
                    console.log(json);
                    currentComponent.setState({
                       
                        profile: json.Data
                    }); 
                  resolve(json);
                });
              }
            }).catch(err=>{
                console.log(err)
            });
        });
    

    };



    render() {
        return (
            <div>
                <div className="container">
                    <h5 className="text-left">Notification 4</h5>

                    <div className="m-b-lg" id="ProfileBriefWidget">
                        <div className="widget-head-color-box navy-bg p-lg text-center m-t-none">
                            <div className="m-b-md">
                                <h2 className="font-bold no-margins" >{this.state.profile.EmpFullName} </h2>
                                <small data-bind="text: ProfileBrief.Designation()">Software Engineer</small>
                            </div>
                            <img className="img-circle circle-border-sm img-lg  m-b-md" alt=""/>

                                <div className="hbs_Socialmedia">
                                    <ul className="list-inline">
                                        <li className="hbsicons"  > <a href="#"><span><i className="fa fa-facebook fa-lg "></i></span></a></li>
                                        <li className="hbsicons"  > <a href="#"><span><i className="fa fa-twitter fa-lg "></i></span></a>  </li>
                                        <li className="hbsicons"  > <a href="#"><span><i className="fa fa-linkedin fa-lg "></i></span></a> </li>
                                        <li className="hbsicons" ><a href="#"> <span><i className="fa fa-skype fa-lg "></i></span></a>  </li>
                                    </ul>
                                </div>
                        </div>

                            <div className="widget-text-box">
                                <dl className="row m-b-xs">
                                    <div className="col-sm-6 ">
                                        <dt>Employee Number :</dt>
                                    </div>
                                    <div className="col-sm-6 text-left">
                                        <dd className="m-b-xs"><span data-bind="text: ProfileBrief.EmployeeDisplayNumber()">{this.state.profile.EmpNo}</span></dd>
                                    </div>
                                </dl>
                                <dl className="row m-b-xs" data-bind="visible: ProfileBrief.ShowMobile" >
                                    <div className="col-sm-6 ">
                                        <dt>Mobile :</dt>
                                    </div>
                                    <div className="col-sm-6 text-left">
                                        <dd className="m-b-xs"><span data-bind="text: ProfileBrief.Mobile()">0717492917</span></dd>
                                    </div>
                                </dl>
                                <dl className="row m-b-xs" data-bind="visible: ProfileBrief.ShowOfficeEmail" >
                                    <div className="col-sm-6 ">
                                        <dt>Email :</dt>
                                    </div>
                                    <div className="col-sm-6 text-left">
                                        <dd className="m-b-xs"><span data-bind="text: ProfileBrief.OfficeEmail">chanakasa@hsenid.lk</span></dd>
                                    </div>
                                </dl>
                                <dl className="row m-b-xs" data-bind="visible: ProfileBrief.ShowAddress" >
                                    <div className="col-sm-6 ">
                                        <dt>Address :</dt>
                                    </div>
                                    <div className="col-sm-6 text-left">
                                        <dd className="m-b-xs"><span data-bind="text: ProfileBrief.Address">31/A, Handagiriya, Balangoda</span></dd>
                                    </div>
                                </dl>
                                <dl className="row m-b-xs" data-bind="visible: ProfileBrief.ShowConfigCol1" >
                                    <div className="col-sm-6 ">
                                        <dt data-bind="text:ProfileBrief.ConfigCol1">null :</dt>
                                    </div>
                                    <div className="col-sm-6 text-left">
                                        <dd className="m-b-xs"><span data-bind="text: ProfileBrief.ConfigCol1Value"></span></dd>
                                    </div>
                                </dl>
                                <dl className="row m-b-xs" data-bind="visible: ProfileBrief.ShowConfigCol2" >
                                    <div className="col-sm-6 ">
                                        <dt data-bind="text:ProfileBrief.ConfigCol2">null :</dt>
                                    </div>
                                    <div className="col-sm-6 text-left">
                                        <dd className="m-b-xs"><span data-bind="text: ProfileBrief.ConfigCol2Value"></span></dd>
                                    </div>
                                </dl>
                            </div>
                
                        </div>


                    </div>



                </div>
        );
    }
}

export default Notification;