import React from 'react';
import * as microsoftTeams from "@microsoft/teams-js";
import querystring from 'querystring';
import './../App.css';



class Notification extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            password: '',
            userId: '',
            data: []
        }

    }

    componentDidMount() {


        var token = localStorage.getItem("token");

        if (!token) {
            alert('you should log in.');
        }

        let currentComponent = this;

        const url2 = "http://localhost/MsTeam/api/HsEmployee/GetTypeWisePendingSummary";
        //const url2 = "http://localhost/MsTeam/api/HsEmployee/authclientuser"
        const params = {
            "token": localStorage.getItem("token"),
            "empnumber": localStorage.getItem("empNumber")
        };

        fetch(url2, {
            method: "POST",
            body: querystring.stringify(params),
            headers: {
                Accept: "application/json",
                "Content-Type": "application/x-www-form-urlencoded",
                //   "auth_token" : "a23b2e98-bed1-4e09-b729-2fc8527a8c78"
            }
        }).then(result => {

            if (result.status !== 200) {
                result.json().then(json => {
                    // TODO: Check explicitly for invalid_grant or interaction_required
                    console.log('error notification');

                });
            } else {
                result.json().then(json => {
                    var data = json
                    currentComponent.setState({

                        data: json
                    });
                    console.log(data)


                });
            }
        }).catch(err => {
            console.log(err)
        });

    };

    handleChange = (e) => {
        this.setState({ [e.target.name]: e.target.value });
    };

    logout = () => {
        localStorage.removeItem('token')
    }

    loadnotification = () => {
        let currentComponent = this;

        const url2 = "http://localhost/MsTeam/api/HsEmployee/GetTypeWisePendingSummary";
        //const url2 = "http://localhost/MsTeam/api/HsEmployee/authclientuser"
        const params = {
            "token": localStorage.getItem("token"),
            "empnumber": localStorage.getItem("empNumber")
        };

        fetch(url2, {
            method: "POST",
            body: querystring.stringify(params),
            headers: {
                Accept: "application/json",
                "Content-Type": "application/x-www-form-urlencoded",
                //   "auth_token" : "a23b2e98-bed1-4e09-b729-2fc8527a8c78"
            }
        }).then(result => {

            if (result.status !== 200) {
                result.json().then(json => {
                    // TODO: Check explicitly for invalid_grant or interaction_required
                    console.log('error notification');

                });
            } else {
                result.json().then(json => {
                    var data = json
                    currentComponent.setState({

                        data: json
                    });
                    console.log(data)


                });
            }
        }).catch(err => {
            console.log(err)
        });

    }

    render() {
        return (
            <div>
              
                <div className="row justify-content-center">
                    <h3 className="text-center text-white pt-5">Work flow notification</h3>
                    <div className="col-4">
                        <div className="col-md-12">
                            <div className="card flex-md-row mb-4 shadow-sm h-md-250">
                                <div className="card-body d-flex flex-column align-items-start">
                                    <strong className="d-inline-block mb-2 text-primary">World</strong>
                                    <h6 className="mb-0">
                                        <a className="text-dark" href="#">40 Percent of People Can’t Afford Basics</a>
                                    </h6>
                                    <div className="mb-1 text-muted small">Nov 12</div>
                                    <p className="card-text mb-auto">This is a wider card with supporting text below as a natural lead-in to additional content.</p>
                                    <a className="btn btn-outline-primary btn-sm" role="button" href="http://www.jquery2dotnet.com/">Continue reading</a>
                                </div>
                                <img className="card-img-right flex-auto d-none d-lg-block" alt="Thumbnail [200x250]" src="//placeimg.com/250/250/arch" width="200px" height="250" />
                            </div>
                        </div>

                        <div className="col-md-12">
                            <div className="card flex-md-row mb-4 shadow-sm h-md-250">
                                <div className="card-body d-flex flex-column align-items-start">
                                    <strong className="d-inline-block mb-2 text-primary">Health</strong>
                                    <h6 className="mb-0">
                                        <a className="text-dark" href="#">Food for Thought: Diet and Brain Health</a>
                                    </h6>
                                    <div className="mb-1 text-muted small">Nov 12</div>
                                    <p className="card-text mb-auto">This is a wider card with supporting text below as a natural lead-in to additional content.</p>
                                    <a className="btn btn-outline-primary btn-sm" role="button" href="http://www.jquery2dotnet.com/">Continue reading</a>
                                </div>
                                <img className="card-img-right flex-auto d-none d-lg-block" alt="Thumbnail [200x250]" src="//placeimg.com/250/250/nature" width="200px" height="250" />
                            </div>
                        </div>

                    </div>
                    <div className="col-4">
                        <div className="table-container">
                            <table className="table table-filter">
                                <tbody>
                                    {this.state.data.map((x, i) =>

                                        <tr id={i} data-status="pagado">
                                            <td>
                                                <div className="ckbox">
                                                    <img src="https://img.icons8.com/metro/26/000000/appointment-reminders.png" className="media-photo" />


                                                </div>
                                            </td>
                                            <td>
                                                <a href="javascript:;" className="star">
                                                    <i className="glyphicon glyphicon-star"></i>
                                                </a>
                                            </td>
                                            <td>
                                                <div className="media">
                                                    <a href="#" className="pull-left">


                                                    </a>
                                                    <div className="media-body">
                                                        <span className="media-meta pull-right"> module Id= {x.TypeCode}</span>
                                                        <h4 className="title">
                                                            {x.TypeName}
                                                            <span className="pull-right pagado">  {x.ModuleCode}</span>
                                                        </h4>
                                                        <p className="summary">Pending Delelte Count = {x.PendingDelCount} </p>
                                                        <p className="summary">Pending Apply Count = {x.PendingAppCount} </p>
                                                    </div>
                                                </div>
                                            </td>
                                        </tr>


                                    )}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Notification;