import React from 'react';
import * as microsoftTeams from "@microsoft/teams-js";
import querystring from 'querystring';
import Workflow from './workflow'
import './../App.css';



class Notification extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            password: '',
            userId: '',
            context: {},
            logged: false
        }

    }

    componentDidMount() {


        let currentComponent = this;

        var token = localStorage.getItem("token");

        if (!token) {
            //alert('you should log in.');
            currentComponent.setState({
                logged: false
            });
        } else {
            currentComponent.setState({
                logged: true
            });
        }



    };

    handleChange = (e) => {
        this.setState({ [e.target.name]: e.target.value });
    };

    submitform = () => {
        let currentComponent = this;

        const url2 = "http://localhost/MsTeam/api/HsEmployee/authclientuser"
        const params = {
            "UserID": this.state.userId,
            "UserPassword": this.state.password,
            "ClientID": 1,
            "GroupID": 1
        };

        fetch(url2, {
            method: "POST",
            body: querystring.stringify(params),
            headers: {
                Accept: "application/json",
                "Content-Type": "application/x-www-form-urlencoded"
            }
        }).then(result => {
            console.log(result)
            if (result.status !== 200) {
                result.json().then(json => {
                    // TODO: Check explicitly for invalid_grant or interaction_required
                    console.log('error notification');

                });
            } else {
                result.json().then(json => {
                    var data = json.Data
                    currentComponent.setState({

                        profile: json.Data
                    });

                    localStorage.setItem("token", data.Token);
                    localStorage.setItem("empNumber", data.EmpNoEncrypted);
                    localStorage.setItem("userName", data.EmpFullName);
                    this.componentDidMount();
                });
            }
        }).catch(err => {
            console.log(err)
        });

    }

    render() {
        return (
            <div>
                {this.state.logged ? <Workflow /> :
                    <div className="container">
      <div id="login">
                    <h3 className="text-center text-white pt-5">Login form</h3>
                    <div className="row justify-content-center">
                        <div className="col-3">

                            <br />
                            <form>
                                <div className="form-group">
                                    <label className="text-light" for="exampleInputEmail1">User Id </label>
                                    <input type="text" className="form-control" name="userId" value={this.state.userId} onChange={this.handleChange} />

                                </div>
                                <br />
                                <div className="form-group">
                                    <label className="text-light"  for="exampleInputPassword1">Password</label>
                                    <input type="password" className="form-control" name="password" value={this.state.password} onChange={this.handleChange} />
                                </div>
                                <br />
                                <button onClick={this.submitform} type="button" className="btn btn-light btn-md">Submit</button>
                            </form>

                        </div>

                    </div>

                </div>
           

                    </div>
                }

           </div>
        );
    }
}

export default Notification;