import React from "react";
import ReactDOM from "react-dom";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  useParams,
  Link
} from "react-router-dom";
import Demo from './component/Demo';
import Demo2 from './component/Demo2';
import Noticiation from './component/notification';
import Auth from './component/auth';

import './App.css';


function BlogPost() {
  let { slug } = useParams();
  return <div>Now showing post {slug}</div>;
}

function logout() {
  localStorage.removeItem('token')
}

function App() {



  return (
    <div className="App">


      <Router>
        <nav className="navbar navbar-expand-lg navbar-light bg-light">
          <div className="container-fluid">
            
            <Link className="navbar-brand" to="/">Hsenid Biz</Link>
            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
              <span className="navbar-toggler-icon"></span>
            </button>
            <div className="collapse navbar-collapse" id="navbarNav">
              <ul className="navbar-nav">
                <li className="nav-item">
                  <Link className="nav-link" to="/React-h-biz">Home</Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="/React-h-biz/demo2">Token</Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link " to="/React-h-biz/notification">Notification</Link>
                </li>
                <li className="nav-item ">
                  <a className="nav-link disabled" href="#" tabindex="-1" aria-disabled="true">Disabled</a>
                </li>
              </ul>
             
            </div>
            <button onClick={logout} className="btn  App-link">Log out</button>
          </div>
        </nav>

        <Switch>
          <Route exact path="/React-h-biz">
            <div>
            <Auth />
            {/* <Home /> */}
            </div>
         
          </Route>
          <Route exact path="/React-h-biz/demo2">
            <div>
            <Demo />
            {/* <Demo2/> */}
            </div>
            
          </Route>
          <Route path="/React-h-biz/notification">
            <Noticiation />
          </Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
